/*------------------------------------*/
/*             CSS RESET              */
/*------------------------------------*/
html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent; }

body {
  line-height: 1; }

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

nav ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent; }

/* change colours to suit your needs */
ins {
  background-color: #ff9;
  color: #000;
  text-decoration: none; }

/* change colours to suit your needs */
mark {
  background-color: #ff9;
  color: #000;
  font-style: italic;
  font-weight: bold; }

del {
  text-decoration: line-through; }

abbr[title], dfn[title] {
  border-bottom: 1px dotted;
  cursor: help; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

/* change border colour to suit your needs */
hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #cccccc;
  margin: 1em 0;
  padding: 0; }

input, select {
  vertical-align: middle; }

* {
  box-sizing: border-box;
  list-style-type: none;
  margin: 0; }

.RobotIcons {
  display: flex;
  width: 100%;
  height: 100%;
  margin-top: 100px; }
  .RobotIcons .robotIconContainer {
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    align-self: center;
    margin: auto; }
    .RobotIcons .robotIconContainer .robotCard {
      display: flex;
      flex-direction: column;
      width: 150px;
      height: 100%;
      margin: 10px;
      margin-top: 20px; }
      .RobotIcons .robotIconContainer .robotCard:nth-of-type(5) {
        min-height: 450px; }
      .RobotIcons .robotIconContainer .robotCard img {
        width: 60px;
        align-self: center;
        background-color: rgba(0, 0, 0, 0.24);
        box-shadow: 0 0 30px 8px rgba(0, 0, 0, 0.3);
        border-radius: 50%; }
      .RobotIcons .robotIconContainer .robotCard p {
        width: 100%;
        text-align: center;
        color: #fff;
        margin-top: 20px;
        font-weight: bold;
        text-decoration: underline; }
      .RobotIcons .robotIconContainer .robotCard h3 {
        width: 100%;
        font-size: 25px;
        text-align: center;
        color: #fff;
        font-weight: bold; }
      .RobotIcons .robotIconContainer .robotCard ul {
        width: 100%;
        margin-top: 15px; }
        .RobotIcons .robotIconContainer .robotCard ul li {
          width: 100%;
          text-align: center;
          margin-top: 5px;
          color: #fff; }
